<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="6" v-for="(site,itemIndex) in vueRecommend" :key="itemIndex">
        <el-card shadow="hover" class="box-card" @click="">
          <a :href="site.siteUrl" target="_blank">
            <img :src="site.siteIcon" class="card-icon">
            <div class="site-content">
              <div class="site-title" :title="site.siteTitle">
                {{site.siteTitle}}
              </div>
              <div class="site-description" :title="site.siteDescription">
                {{site.siteDescription}}
              </div>
            </div>
          </a>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'MainContent',
  data() {
    return {
      vueRecommend: [
        {
          siteIcon: require("@/assets/img/web/vis/d3.png"),
          siteTitle: 'D3.js',
          siteDescription: 'D3.js是用于数据可视化的 js 库',
          siteUrl: "https://d3js.org"
        },
        {
          siteIcon: require("@/assets/img/web/vis/echarts.png"),
          siteTitle: 'ECharts',
          siteDescription: 'ECharts 是一款强大、可交互的绘图和可视化库',
          siteUrl: "https://echarts.apache.org/zh/index.html"
        },
        {
          siteIcon: require("@/assets/img/web/vis/threejs.png"),
          siteTitle: 'three.js',
          siteDescription: 'JavaScript 3D 引擎',
          siteUrl: "https://threejs.org/"
        },
        {
          siteIcon: require("@/assets/img/web/vis/chartjs.svg"),
          siteTitle: 'chartjs',
          siteDescription: '基于 HTML5 的 JavaScript 图表库',
          siteUrl: "https://www.chartjs.org/"
        },
        {
          siteIcon: require("@/assets/img/web/vis/plotjs.png"),
          siteTitle: 'plotly.js',
          siteDescription: '基于 d3.js 和 stack.gl 的开源绘图库',
          siteUrl: "https://plot.ly/javascript/"
        },
        {
          siteIcon: require("@/assets/img/web/vis/spritejs.png"),
          siteTitle: 'SpriteJS',
          siteDescription: 'SpriteJS 是跨平台的高性能图形系统，它能够支持web、node、桌面应用和小程序的图形绘制和实现各种动画效果',
          siteUrl: "https://spritejs.org/#/"
        },
        {
          siteIcon: require("@/assets/img/web/vis/apexcharts.png"),
          siteTitle: 'apexcharts.js',
          siteDescription: '构建于 SVG 的交互 JavaScript 图表',
          siteUrl: "https://apexcharts.com"
        },
        {
          siteIcon: require("@/assets/img/nav_menu/github.png"),
          siteTitle: 'dc.js',
          siteDescription: '数据可视化工具,支持和允许高效展示大型多维数据集',
          siteUrl: "https://github.com/dc-js/dc.js"
        },
        {
          siteIcon: require("@/assets/img/web/vis/f2.png"),
          siteTitle: 'AntV',
          siteDescription: 'AntV 是蚂蚁金服全新一代数据可视化解决方案，致力于提供一套简单方便、专业可靠、无限可能的数据可视化最佳实践',
          siteUrl: "https://antv.vision/zh"
        },
        {
          siteIcon: require("@/assets/img/web/vis/f2.png"),
          siteTitle: 'F2',
          siteDescription: 'F2 是一个专注于移动，开箱即用的可视化解决方案',
          siteUrl: "https://f2.antv.vision/zh"
        },
        {
          siteIcon: require("@/assets/img/web/vis/heatmapjs.png"),
          siteTitle: 'heatmap.js',
          siteDescription: 'Web 的动态热力图',
          siteUrl: "https://www.patrick-wied.at/static/heatmapjs/"
        },
        {
          siteIcon: require("@/assets/img/nav_menu/github.png"),
          siteTitle: 'VivaGraphJS',
          siteDescription: 'JS 的作图库',
          siteUrl: "https://github.com/anvaka/VivaGraphJS"
        },
        {
          siteIcon: require("@/assets/img/nav_menu/github.png"),
          siteTitle: 'dygraphs',
          siteDescription: 'dygraphs 是一个快速、便捷的开源 JavaScript 绘图库，用户可以使用它描述复杂的数据集',
          siteUrl: "http://dygraphs.com/"
        },
        {
          siteIcon: require("@/assets/img/nav_menu/github.png"),
          siteTitle: 'city-roads',
          siteDescription: '任何城市道路的可视化',
          siteUrl: "https://anvaka.github.io/city-roads/"
        },
        {
          siteIcon: require("@/assets/img/web/vis/carto.svg"),
          siteTitle: 'CARTO',
          siteDescription: 'CARTO 是一款开源的、强大的平台用于发现和预测隐藏在位置信息下的关键信息',
          siteUrl: "https://anvaka.github.io/city-roads/"
        },
        {
          siteIcon: require("@/assets/img/web/vis/inmap.png"),
          siteTitle: 'inMap 2.0',
          siteDescription: 'inMap 是一款基于百度地图的大数据可视化库，专注于大数据方向的散点、热力图、网格、聚合等方式展示，致力于让大数据可视化变得简单易用。',
          siteUrl: "https://anvaka.github.io/city-roads/"
        },
        {
          siteIcon: require("@/assets/img/nav_menu/github.png"),
          siteTitle: 'Cal-Heatmap',
          siteDescription: 'Cal-Heatmap 是一款可以做出类似 github 的 contribution calendar 效果的 js 模块',
          siteUrl: "https://anvaka.github.io/city-roads/"
        },
        {
          siteIcon: require("@/assets/img/nav_menu/github.png"),
          siteTitle: 'iDataV',
          siteDescription: '大屏数据可视化案例',
          siteUrl: "https://github.com/yyhsong/iDataV"
        },
      ]
    }
  },
}
</script>

<style>
a {
  text-decoration: none;
  color: #373e4a;
}

.card-icon {
  height: 40px;
  width: 40px;
  float: left;
  margin-right: 10px;
}

.box-card {
  border-radius: 12px;
  height: 90px;
  margin-top: 15px;
  /* display: flex; */
}

.site-title {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
}

.site-description {
  color: #979898;
  font-size: 13px;
  margin: 2px;
  word-break: break-word;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
</style>


